	module.exports = {"data":{"post":{"html":"<p>While helping (well, actually watching) me clean out our basement, Christopher discovered Frauke's typewriter (which he immediately dubbed an \"antique\") and asked if he could try it out. This evening we let him at it.</p>\n<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3290549173\" title=\"View 'CIMG0711' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3635/3290549173_a735405240.jpg\" alt=\"CIMG0711\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>After experimenting a bit, he put in a fresh sheet of paper and spent over an hour preparing a \"report\" on snakes, with lists of species of snakes, what they eat, what eats them, and where they are found. </p>\n<p>Afterwards he declared that, unlike us, he does not need a computer to print things out, he can just do it himself.</p>","fields":{"title":"Journalist Christopher","date":"18 Feb 2009","url":"https://papascott.de/2009-02-18-journalist-christopher/"}}},"pathContext":{"slug":"/2009-02-18-journalist-christopher/","prev":{"fields":{"title":"School Daze","date":"10 Feb 2009","slug":"/2009-02-10-school-daze/"}},"next":{"fields":{"title":"Spring Break","date":"08 Mar 2009","slug":"/2009-03-08-spring-break/"}}}}

